import { useState } from "react";
import React from 'react';


const Formulario = ({guardarBusquedaLetra}) =>{//recibe objt vacio de App.js

    const [busqueda,guardarBusqueda] = useState({
        artista: '',
        cancion: ''
    });

    const [error,guardarError] = useState(false);

    const {artista, cancion} = busqueda;

    ///funcion a cada inut para leer su contenido
    const actualizarStare = e =>{
        guardarBusqueda({
            ...busqueda,
            [e.target.name] : e.target.value
        })
    } 

    //consultar las apis
    const buscarInformacion = e =>{
        e.preventDefault();

        if(artista.trim() === '' || cancion.trim() === ''){
            guardarError(true);
            return;
        }
        guardarError(false);
        /// pasamos al componente principal
        guardarBusquedaLetra(busqueda);

    }


    return(
        <div className="bg-info">
             {error ? <p className="alert alert-danger text-center p-2">Todos los
                    campos son obligatorios</p> : null}
            <div className="container">
                <div className="row">
                   <form
                    onSubmit={buscarInformacion}
                        className="col card text-white bg-transparent mb-5 pt-5 pb-2"
                    >
                        <fieldset>
                            <legend className="text-center">Buscador letras canciones</legend>
                            <div className="row">
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label>Artista</label>
                                        <input
                                        type="text"
                                        className="form-control"
                                        name="artista"
                                        placeholder="Nombre Artista"
                                        onChange={actualizarStare}
                                        value={artista}
                                        />
                                    </div>
                                </div>
                                <div className="col-md-6">
                                    <div className="form-group">
                                        <label>Cancion</label>
                                        <input
                                        type="text"
                                        className="form-control"
                                        name="cancion"
                                        placeholder="Nombre Cancion"
                                        onChange={actualizarStare}
                                        value={cancion}
                                        />
                                    </div>
                                </div>
                            </div>
                            <button 
                            type ="submit"
                            className="btn btn-primary float-right"
                            >Buscar</button>
                        </fieldset>
                    </form>
                </div>
            </div>
        </div>
        );


}

export default Formulario;